___CSS_LOADER_EXPORT___.push([module.id, `*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
body{
    background-color: bisque;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
#container{
    border: 1px solid;
    height: 95vh;
    width: 95vw;
    border-radius: 2vw;
    overflow: hidden;
    /* padding: 2vw; */
    background-color: azure;

    display: flex;
}
#container > * {
    padding: 2vw;
}
.nav_side{
    background-color: red;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.details{
    background-color: aquamarine;
    flex: 4;
}
fieldset{
    padding: .8vh 1vw;
    border-radius: 1vw;
}
legend{
    font-weight: bold;
    font-family: 'Courier New', Courier, monospace;
    padding: .2vw;
}
#container ul{
    list-style: none;
    display: flex;
    flex-direction: column;
}
#container li {
}
#container button{
    padding: .5vh 1vw;
    font-weight: bold;
    border-radius: 1vw;
    border: none;
    box-shadow: .3vw .2vw .1vw;
    width: 100%;
}
.notification{
    background-color: aliceblue;
    width: fit-content;
    border-radius: 2vw;
    overflow: hidden;
    text-align: center;
    position: absolute;
    z-index: 50;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.notification h1{
    background-color: red;
    padding: 1vh 2vw;
}
.notification p{
    padding: 1vh 2vw;
}
#project_form, #todo_form{
    display: flex;
    flex-direction: column;
    gap: 1vh;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,UAAU;IACV,SAAS;AACb;AACA;IACI,wBAAwB;IACxB,aAAa;IACb,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;AACA;IACI,iBAAiB;IACjB,YAAY;IACZ,WAAW;IACX,kBAAkB;IAClB,gBAAgB;IAChB,kBAAkB;IAClB,uBAAuB;;IAEvB,aAAa;AACjB;AACA;IACI,YAAY;AAChB;AACA;IACI,qBAAqB;IACrB,OAAO;IACP,aAAa;IACb,sBAAsB;IACtB,8BAA8B;AAClC;AACA;IACI,4BAA4B;IAC5B,OAAO;AACX;AACA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;AACA;IACI,iBAAiB;IACjB,8CAA8C;IAC9C,aAAa;AACjB;AACA;IACI,gBAAgB;IAChB,aAAa;IACb,sBAAsB;AAC1B;AACA;AACA;AACA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,kBAAkB;IAClB,YAAY;IACZ,0BAA0B;IAC1B,WAAW;AACf;AACA;IACI,2BAA2B;IAC3B,kBAAkB;IAClB,kBAAkB;IAClB,gBAAgB;IAChB,kBAAkB;IAClB,kBAAkB;IAClB,WAAW;IACX,QAAQ;IACR,SAAS;IACT,gCAAgC;AACpC;AACA;IACI,qBAAqB;IACrB,gBAAgB;AACpB;AACA;IACI,gBAAgB;AACpB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;AACZ","sourcesContent":["*{\n    box-sizing: border-box;\n    padding: 0;\n    margin: 0;\n}\nbody{\n    background-color: bisque;\n    height: 100vh;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n#container{\n    border: 1px solid;\n    height: 95vh;\n    width: 95vw;\n    border-radius: 2vw;\n    overflow: hidden;\n    /* padding: 2vw; */\n    background-color: azure;\n\n    display: flex;\n}\n#container > * {\n    padding: 2vw;\n}\n.nav_side{\n    background-color: red;\n    flex: 1;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n}\n.details{\n    background-color: aquamarine;\n    flex: 4;\n}\nfieldset{\n    padding: .8vh 1vw;\n    border-radius: 1vw;\n}\nlegend{\n    font-weight: bold;\n    font-family: 'Courier New', Courier, monospace;\n    padding: .2vw;\n}\n#container ul{\n    list-style: none;\n    display: flex;\n    flex-direction: column;\n}\n#container li {\n}\n#container button{\n    padding: .5vh 1vw;\n    font-weight: bold;\n    border-radius: 1vw;\n    border: none;\n    box-shadow: .3vw .2vw .1vw;\n    width: 100%;\n